.page {
  font-family: 'Inter', sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 700px;
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateX(-100%);

    transition: transform 0.3s;
    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 125px;
  }

  &__section-title {
    font-weight: 700;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    margin-bottom: 48px;

    @include onTablet {
      font-size: 40px;
      line-height: 40px;
    }

    @include onDesktop {
      margin-bottom: 64px;
      font-size: 48px;
      line-height: 48px;
    }

    &--left {
      text-align: left;
    }
  }
}
